import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { CopyToClipboard, Spinner } from '../common';
import autobind from 'autobind-decorator';

import { BrightcovePlayerProps, BrightcovePlayer } from '../brightcove-player';
import { BrightcovePlayerControls } from '../brightcove-player';

import { SPUtility, SPAddinContext, SharePointApiGateway, SPEmbedUtil } from '../../services/sharepoint';
import { BCPlayer } from '@brightcove/video-cloud-sdk';

import * as FileSaver from 'file-saver';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload, faUpload, faPlus } from '@fortawesome/fontawesome-free-solid';
import * as moment from 'moment';

export class MiniVideoEmbed extends React.Component<MiniVideoEmbedProps, MiniVideoEmbedState> {
    constructor(props: MiniVideoEmbedProps, context?: any) {
        super(props, context);

        this.state = {
            isWebPartUploading: false
        };
    }

    @autobind
    handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        const { onPlayerPropsChanged } = this.props;

        this.setState({
            modifiedEmbedCode: undefined
        });

        if (onPlayerPropsChanged) {
            onPlayerPropsChanged(bcPlayerProps);
        }
    }

    @autobind
    handleEmbedCodeChange(embedCodeInput: React.ChangeEvent<HTMLTextAreaElement>) {
        this.setState({
            modifiedEmbedCode: embedCodeInput.target.value
        });
    }

    @autobind
    async addWebPartToPage() {
        const { spApiGateway, spAddinContext } = this.props;
        const { modifiedEmbedCode } = this.state;

        if (!spApiGateway || !spAddinContext) {
            return;
        }

        const embedCode = modifiedEmbedCode || this.generateVideoEmbedCode();
        await spApiGateway.spEmbedUtil.insertContentEditorWebPart(
            spAddinContext.Referrer,
            'Brightcove Video WebPart',
            'Brightcove Web Part to embed a video onto a web part page.',
            embedCode || 'An error occurred.'
        );
    }

    getWebPartTitle() {
        const { bcPlayerProps, accountId, playerId, videoId, videoTitle } = this.props;

        let webPartName = 'Brightcove Video WebPart';
        if (videoTitle) {

            let embedType = 'javascript';
            if (bcPlayerProps.embedType === 'Standard') {
                embedType = 'iframe';
            }

            webPartName = `${videoTitle}-${accountId}-${playerId}-${videoId}-${embedType}`;
        }

        return webPartName;
    }

    @autobind
    async uploadWebPartToGallery() {
        const { spApiGateway, spAddinContext } = this.props;
        const { modifiedEmbedCode } = this.state;

        if (!spApiGateway || !spAddinContext) {
            return;
        }

        this.setState({
            isWebPartUploading: true
        });

        const embedCode = modifiedEmbedCode || this.generateVideoEmbedCode();
        let webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Brightcove Web Part to embed a video onto a web part page.', embedCode || 'An error occurred.');

        try {
            const dt = moment();
            const filename = `BrightcoveVideoWebPart-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
            await spApiGateway.spEmbedUtil.uploadWebPartToGallery(webPart, filename, 'Brightcove Video Web Parts');
        } catch (ex) {
            // Do Nothing.
        }

        SPUtility.closeParentDialog(true);
    }

    @autobind
    downloadScriptEditorWebPart() {
        const { modifiedEmbedCode } = this.state;

        const embedCode = modifiedEmbedCode || this.generateVideoEmbedCode();
        const webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Brightcove Web Part to embed a video onto a web part page.', embedCode || 'An error occurred.');

        const dt = moment();
        const filename = `BrightcoveVideoWebPart-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
        FileSaver.saveAs(new Blob([webPart], { type: 'text/plain;charset=utf-8' }), filename);
    }

    generateVideoWebPart() {
        const { spAddinContext, accountId, playerId, videoId, bcPlayerProps, videoTitle } = this.props;

        if (!spAddinContext) {
            return 'An error occurred.';
        }

        return SPEmbedUtil.generateVideoWebPart(spAddinContext.Referrer, accountId, playerId || 'default', videoId, bcPlayerProps.autoplay || false, bcPlayerProps.muted || false, videoTitle, 'Brightcove Web Part to embed a video onto a web part page.');
    }

    generateVideoEmbedCode() {
        const { accountId, playerId, videoId, bcPlayerProps } = this.props;

        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={accountId} playerId={playerId} mode="Video" modeId={videoId} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        return embedCode;
    }

    render() {
        const {
            bcPlayerProps,
            accountId,
            playerId,
            videoId,
            defaultVideoPlayerId,
            showCloseDialog,
            doesEmbedHelperExist,
            showAddWebPartButton,
            showUploadWebPartButton,
            showDownloadWebPartButton,
            retrieveAllPlayers,
        } = this.props;

        const { isWebPartUploading, modifiedEmbedCode } = this.state;

        const playerPreviewUrl = `https://players.brightcove.net/${accountId}/${playerId}_default/index.html?videoId=${videoId}`;
        const embedCode = modifiedEmbedCode || this.generateVideoEmbedCode();

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Preview in browser</label>
                    <div className={controlClass + ' word-wrap'}>
                        <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                    </div>
                </div>
                <BrightcovePlayerControls
                    accountId={accountId}
                    retrieveAllPlayers={retrieveAllPlayers}
                    defaultPlayerId={defaultVideoPlayerId}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm small"
                    whClass="col-xs-6"
                    bcPlayerProps={bcPlayerProps}
                    onPlayerPropsChanged={this.handlePlayerPropsChanged}
                />
                <div className="col-xs-12 small">
                    <div>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</div>
                    <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                    <CopyToClipboard />
                    {showDownloadWebPartButton === true &&
                        <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                            <FontAwesomeIcon icon={faDownload} /> Download Web Part
                        </Button>
                    }
                    {isWebPartUploading &&
                        <Spinner loadingText="Uploading..." />
                    }
                    {showUploadWebPartButton === true && !isWebPartUploading &&
                        <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} disabled={!doesEmbedHelperExist} onClick={this.uploadWebPartToGallery} title={doesEmbedHelperExist ? '' : 'Unable to upload web part as the Brightcove Embed Helper is not installed.'}>
                            <FontAwesomeIcon icon={faUpload} /> Upload Web Part to Gallery
                        </Button>
                    }
                    {showAddWebPartButton === true &&
                        <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} disabled={!doesEmbedHelperExist} onClick={this.addWebPartToPage} title={doesEmbedHelperExist ? '' : 'Unable to insert web part as the Brightcove Embed Helper is not installed.'}>
                            <FontAwesomeIcon icon={faPlus} /> Insert Web Part
                        </Button>
                    }
                </div>
                {showCloseDialog &&
                    <div className="col-xs-12 small" style={{ paddingTop: '5px' }}>
                        <Button className="pull-right" bsSize="small" onClick={() => SPUtility.closeParentDialog(false)}>
                            Close Dialog
                        </Button>
                    </div>
                }
            </>
        );
    }
}

export interface MiniVideoEmbedProps {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    accountId: string;
    playerId?: string;
    videoId: string;
    videoTitle: string;
    defaultVideoPlayerId: string;
    showCloseDialog: boolean;

    spAddinContext?: SPAddinContext;
    spApiGateway?: SharePointApiGateway;
    doesEmbedHelperExist?: boolean;
    showAddWebPartButton?: boolean;
    showUploadWebPartButton?: boolean;
    showDownloadWebPartButton?: boolean;

    retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
    onPlayerPropsChanged: (bcPlayerProps: Partial<BrightcovePlayerProps>) => void;
}

export interface MiniVideoEmbedState {
    isWebPartUploading: boolean;
    modifiedEmbedCode?: string;
}


