import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { CopyToClipboard } from '../common/CopyToClipboard';
import autobind from 'autobind-decorator';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload, faUpload, faPlus } from '@fortawesome/fontawesome-free-solid';

import { BrightcoveExperience, BrightcoveExperienceProps, getDefaultBrightcoveExperienceProps, BrightcoveExperienceControls, BrightcoveExperienceOverrideMode } from '../brightcove-experience';

import { BCExperience } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';
import { SPEmbedUtil, SPUtility } from 'services/sharepoint';

import * as FileSaver from 'file-saver';
import { Spinner } from '../common';
import * as moment from 'moment';

export class ExperienceEmbed extends React.Component<ExperienceEmbedProps, ExperienceEmbedState> {
    constructor(props: ExperienceEmbedProps, context?: any) {
        super(props, context);

        let bcExperienceProps = getDefaultBrightcoveExperienceProps();
        bcExperienceProps.sharepointVersion = props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            isWebPartUploading: false,
            bcExperienceProps
        };
    }

    @autobind
    handleExperiencePropsChanged(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
        this.setState({
            bcExperienceProps,
            modifiedEmbedCode: undefined
        });
    }

    @autobind
    handleEmbedCodeChange(embedCodeInput: React.ChangeEvent<HTMLTextAreaElement>) {
        this.setState({
            modifiedEmbedCode: embedCodeInput.target.value
        });
    }

    @autobind
    async addWebPartToPage() {
        const { connectorContext } = this.props;
        const { modifiedEmbedCode } = this.state;
        const { spApiGateway } = connectorContext;

        const embedCode = modifiedEmbedCode || this.generateExperienceEmbedCode();

        await spApiGateway.spEmbedUtil.insertContentEditorWebPart(
            connectorContext.spAddinContext.Referrer,
            'Brightcove Experience WebPart',
            'Brightcove Web Part to embed an experience onto a web part page.',
            embedCode || 'An error occurred.'
        );
    }

    getWebPartTitle() {
        const { connectorContext, experience } = this.props;
        const { selectedAccount } = connectorContext;
        const { bcExperienceProps } = this.state;
        let webPartName = 'Brightcove Experience WebPart';

        if (!experience || !selectedAccount) {
            return webPartName;
        }

        if (experience.name) {

            let embedType = 'javascript';
            if (bcExperienceProps.embedType === 'Standard') {
                embedType = 'iframe';
            }

            webPartName = `${experience.name}-${selectedAccount.accountId}-${experience.id}-${embedType}`;
        }
        return webPartName;
    }

    @autobind
    async uploadWebPartToGallery() {
        const { connectorContext, experience } = this.props;
        const { selectedAccount, spApiGateway, spAddinContext } = connectorContext;
        const { modifiedEmbedCode } = this.state;

        if (!spApiGateway || !spAddinContext || !experience || !selectedAccount) {
            return;
        }

        this.setState({
            isWebPartUploading: true
        });

        const embedCode = modifiedEmbedCode || this.generateExperienceEmbedCode();
        const webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Brightcove Web Part to embed an experience onto a web part page.', embedCode || 'An error occurred.');

        try {
            const dt = moment();
            const filename = `BrightcoveExperienceWebPart-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
            await spApiGateway.spEmbedUtil.uploadWebPartToGallery(webPart || 'An error occurred.', filename, 'Brightcove Experience Web Parts');
        } catch (ex) {
            // Do Nothing.
        }

        SPUtility.closeParentDialog(true);
    }

    @autobind
    downloadScriptEditorWebPart() {
        const { connectorContext, experience } = this.props;
        const { modifiedEmbedCode } = this.state;
        const { selectedAccount } = connectorContext;

        if (!experience || !selectedAccount) {
            return;
        }

        const embedCode = modifiedEmbedCode || this.generateExperienceEmbedCode();
        const webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Brightcove Web Part to embed an experience onto a web part page.', embedCode || 'An error occurred.');

        const dt = moment();
        const filename = `BrightcoveExperienceWebPart-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
        FileSaver.saveAs(new Blob([webPart], { type: 'text/plain;charset=utf-8' }), filename);
    }

    generateExperienceWebPart() {
        const { connectorContext, experience } = this.props;
        const { selectedAccount } = connectorContext;
        const { bcExperienceProps } = this.state;

        if (!experience || !selectedAccount) {
            return 'An error occurred.';
        }

        return SPEmbedUtil.generateExperienceWebPart(
            connectorContext.spAddinContext.Referrer,
            selectedAccount.accountId,
            experience.id,
            bcExperienceProps.overrideExperienceVideos || false,
            bcExperienceProps.experienceOverrideMode || BrightcoveExperienceOverrideMode.Videos,
            bcExperienceProps.experienceOverrideModeIds || [],
            experience.name,
            'Brightcove Web Part to embed an experience onto a web part page.'
        );
    }

    generateExperienceEmbedCode() {
        const { connectorContext, experience } = this.props;
        const { bcExperienceProps } = this.state;

        if (!connectorContext.selectedAccount || !experience) {
            return;
        }

        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcoveExperience accountId={connectorContext.selectedAccount.accountId} experienceId={experience.id} experience={experience} {...bcExperienceProps as BrightcoveExperienceProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        return embedCode;
    }

    render() {
        const {
            layoutMode,
            connectorContext,
            experience,
            labelClass,
            controlClass,
            hideExperienceName,
            showAddWebPartButton,
            showUploadWebPartButton,
            showDownloadWebPartButton,
        } = this.props;

        const { isWebPartUploading, bcExperienceProps, modifiedEmbedCode } = this.state;

        if (!experience) {
            return (
                <div className="text-center">
                    <i>An experience has not been selected. Please select an experience above to obtain the embed code.</i>
                </div>
            );
        }

        if (!connectorContext.selectedAccount) {
            return (
                <div className="text-center">
                    <i>An account has not been selected. Please select an account above.</i>
                </div>
            );
        }

        const experienceSharableUrl = `https://players.brightcove.net/${connectorContext.selectedAccount.accountId}/experience_${experience.id}/share.html`;
        const embedCode = modifiedEmbedCode || this.generateExperienceEmbedCode();

        let layoutSize = 'Responsive';
        if (experience.width) {
            layoutSize = `Fixed width of ${experience.width}px`;
        }

        if (experience.height) {
            layoutSize += ` and height of ${experience.height}px`;
        }

        return (
            <>
                {(hideExperienceName === undefined || hideExperienceName === false) &&
                    <div className="form-group">
                        <label className={labelClass}>Experience Name</label>
                        <div className={controlClass}>{experience.name}</div>
                    </div>
                }
                {layoutMode === 'Full' &&
                    <iframe className="col-md-12" src={experience.previewUrl} style={{ height: '400px' }} />
                }
                <div className="form-group">
                    <label className={labelClass}>Shareable URL</label>
                    <div className={controlClass}><a href={experienceSharableUrl} target="_blank">{experienceSharableUrl}</a></div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Experience-defined Layout Size</label>
                    <div className={controlClass}>{layoutSize}</div>
                </div>
                {layoutMode === 'Mini' &&
                    <>
                        <BrightcoveExperienceControls
                            connectorContext={connectorContext}
                            labelClass={labelClass}
                            controlClass={controlClass}
                            formControlClass="input-sm"
                            bcExperienceProps={bcExperienceProps}
                            onExperiencePropsChanged={this.handleExperiencePropsChanged}
                        />
                        <div className="form-group">
                            <label className={labelClass}>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</label>
                            <div className={controlClass}>
                                {/* {!bcExperienceProps.overrideExperienceVideos && !bcExperienceProps.overrideHeight && !bcExperienceProps.overrideWidth &&
                                    <div>After your experience has been published once, additional published changes will be included in the existing embed code. You do not need to re-copy and paste the embed code into existing locations.</div>
                                }
                                {(bcExperienceProps.overrideExperienceVideos || bcExperienceProps.overrideHeight || bcExperienceProps.overrideWidth) &&
                                    <div>This experience has been customized. Any changes made will need to be re-copied and pasted into existing locations.</div>
                                } */}
                                <textarea id="embedCode" style={{ width: '100%', height: '80px' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                                <CopyToClipboard />
                                {showDownloadWebPartButton === true &&
                                    <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                                        <FontAwesomeIcon icon={faDownload} /> Download Web Part
                                    </Button>
                                }
                                {isWebPartUploading &&
                                    <Spinner loadingText="Uploading..." />
                                }
                                {showUploadWebPartButton === true && !isWebPartUploading &&
                                    <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} disabled={!connectorContext.doesEmbedHelperExist} onClick={this.uploadWebPartToGallery} title={connectorContext.doesEmbedHelperExist ? '' : 'Unable to upload web part as the Brightcove Embed Helper is not installed.'}>
                                        <FontAwesomeIcon icon={faUpload} /> Upload Web Part to Gallery
                                    </Button>
                                }
                                {showAddWebPartButton === true &&
                                    <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} disabled={!connectorContext.doesEmbedHelperExist} onClick={this.addWebPartToPage} title={connectorContext.doesEmbedHelperExist ? '' : 'Unable to insert web part as the Brightcove Embed Helper is not installed.'}>
                                        <FontAwesomeIcon icon={faPlus} /> Insert Web Part
                                    </Button>
                                }
                            </div>
                        </div>
                    </>
                }
                {layoutMode === 'Full' &&
                    <>
                        <div className="row">
                            <div className="col-md-6">
                                <h4>Copy Code</h4>
                                <div>Copy the appropriate code below and paste it into your web page or app.</div>
                                <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                                <CopyToClipboard />
                                {showDownloadWebPartButton === true &&
                                    <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                                        <FontAwesomeIcon icon={faDownload} /> Download Web Part
                                    </Button>
                                }
                            </div>
                            <div className="col-md-6">
                                <BrightcoveExperienceControls
                                    connectorContext={connectorContext}
                                    labelClass={labelClass}
                                    controlClass={controlClass}
                                    formControlClass="input-sm"
                                    bcExperienceProps={bcExperienceProps}
                                    onExperiencePropsChanged={this.handleExperiencePropsChanged}
                                />
                            </div>
                        </div>
                    </>
                }
            </>
        );
    }
}

export interface ExperienceEmbedProps {
    connectorContext: ConnectorContext;
    experience?: BCExperience;
    layoutMode: 'Mini' | 'Full';
    hideExperienceName?: boolean;
    labelClass?: string;
    controlClass?: string;
    showAddWebPartButton?: boolean;
    showUploadWebPartButton?: boolean;
    showDownloadWebPartButton?: boolean;
}

export interface ExperienceEmbedState {
    isWebPartUploading: boolean;
    bcExperienceProps: Partial<BrightcoveExperienceProps>;
    modifiedEmbedCode?: string;
}


