import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import autobind from 'autobind-decorator';
import { CopyToClipboard } from '../common';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/fontawesome-free-solid';

import { BrightcovePlayerProps, BrightcovePlayer, getDefaultBrightcovePlayerProps } from '../brightcove-player';
import { BrightcovePlayerControls } from '../brightcove-player';

import { ConnectorContext } from '../../services/connector';
import { Account, SPEmbedUtil } from '../../services/sharepoint';

import * as FileSaver from 'file-saver';

export class PlaylistEmbed extends React.Component<PlaylistEmbedProps, PlaylistEmbedState> {
    constructor(props: PlaylistEmbedProps, context?: any) {
        super(props, context);

        let bcPlayerProps = getDefaultBrightcovePlayerProps();
        bcPlayerProps.mode = 'Playlist';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            bcPlayerProps,
        };
    }

    @autobind
    handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps,
            modifiedEmbedCode: undefined
        });
    }

    @autobind
    handleEmbedCodeChange(embedCodeInput: React.ChangeEvent<HTMLTextAreaElement>) {
        this.setState({
            modifiedEmbedCode: embedCodeInput.target.value
        });
    }

    @autobind
    downloadScriptEditorWebPart() {
        const { account, playlistTitle, playlistId } = this.props;
        const { bcPlayerProps, modifiedEmbedCode } = this.state;

        const embedCode = modifiedEmbedCode || this.generatePlaylistEmbedCode();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(playlistTitle || 'Brightcove Playlist WebPart', 'Brightcove Web Part to embed a playlist onto a web part page.', embedCode || 'An error occurred.');

        FileSaver.saveAs(new Blob([webPart], { type: 'text/plain;charset=utf-8' }), `BrightcovePlaylistWebPart-${account.accountId}-${bcPlayerProps.playerId}-${playlistId}.webpart`);
    }

    generatePlaylistEmbedCode() {
        const { account, playlistId } = this.props;
        const { bcPlayerProps } = this.state;

        return ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={account.accountId} playerId={bcPlayerProps.playerId} mode="Playlist" modeId={playlistId} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');
    }

    render() {
        const { connectorContext, account, playlistId, showDownloadWebPart } = this.props;
        const { bcPlayerProps, modifiedEmbedCode } = this.state;

        const playerPreviewUrl = `https://players.brightcove.net/${account.accountId}/${bcPlayerProps.playerId ? bcPlayerProps.playerId : 'default'}_default/index.html?playlistId=${playlistId}`;
        const embedCode = modifiedEmbedCode || this.generatePlaylistEmbedCode();

        return (
            <>
                <div className="row">
                    <div className="form-group">
                        <div>
                            <iframe
                                style={{ width: '100%', height: '400px' }}
                                src={`//players.brightcove.net/${account.accountId}/${bcPlayerProps.playerId ? bcPlayerProps.playerId : 'default'}_default/index.html?playlistId=${playlistId}`}
                            />
                        </div>
                    </div>
                    <div className="form-group">
                        <label>Preview in browser</label>
                        <div>
                            <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <h4>Copy Code</h4>
                        <div>Copy the appropriate code below and paste it into your web page or app.</div>
                        <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                        <CopyToClipboard />
                        {showDownloadWebPart === true &&
                            <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                                <FontAwesomeIcon icon={faDownload} /> Download Web Part
                            </Button>
                        }
                    </div>
                    <div className="col-md-6">
                        Change Player Size<br /><br />
                        <BrightcovePlayerControls
                            accountId={connectorContext.selectedAccount && connectorContext.selectedAccount.accountId}
                            retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                            defaultPlayerId={connectorContext.selectedAccount && connectorContext.selectedAccount.defaultPlaylistPlayerId}
                            bcPlayerProps={bcPlayerProps}
                            onPlayerPropsChanged={this.handlePlayerPropsChanged}
                        />
                    </div>
                </div>
            </>
        );
    }
}

export interface PlaylistEmbedProps {
    connectorContext: ConnectorContext;
    account: Account;
    playlistId: string;

    playlistTitle?: string;
    showDownloadWebPart?: boolean;
}

export interface PlaylistEmbedState {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    modifiedEmbedCode?: string;
}


